/* global Parse */
import React from "react";
import rtClient from '../library/Realtime'
import Chat from '../components/DataGrid/Chat'
import Parse from '../library/parse'
class Home extends React.Component {
    constructor(props) {
        super(props)
        this.state = {
            chatMessages: []
        }
    }

    componentDidMount() {
        rtClient.open()
        const query = new Parse.Query('Event');
        query.ascending('createdAt').limit(5);
        const subscription = rtClient.subscribe(query);
        const handleWS = (todo) => {
          console.log(todo)
          this.setState({
            
              chatMessages: this.state.chatMessages.concat([
                  { message: todo.get('payload').message }
              ])
          })
        }
        subscription.on('create', handleWS);
    }
    render() {

        return (
         <>
           <Chat data={this.state.chatMessages} />
         </>
       );
         }
} 


export default Home;
