import React from "react";
import {

  DataTable,

  Text,

} from "grommet";
const Component = ({ data }) => {
  return (
    <DataTable
      columns={[
        {
          property: "message",
          header: <Text>Message</Text>,
          primary: true,
          render: (d) => {
            return <Text>{ d.message }</Text>
          }
        },
      ]}
      data={data}
    />
  );
};

export default Component;
